import React, { useState } from 'react';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import Portfolio01 from '../images/portfolio-01.jpg';
import Portfolio02 from '../images/portfolio-02.jpg';
import Portfolio03 from '../images/portfolio-03.jpg';
import Portfolio04 from '../images/portfolio-04.jpg';
import Section from './section';
import { InView } from 'react-intersection-observer';

const light = '#fff';

const portfolioData = [
  {
    caption: 'Audi',
    image: Portfolio01
  },
  {
    caption: 'NISSAN GT-R',
    image: Portfolio02
  },
  {
    caption: 'BMW',
    image: Portfolio03
  },
  {
    caption: 'DUCATI 1098',
    image: Portfolio04
  }
];

const Portfolio = () => {
  const [photoIndex, setPhotoIndex] = useState(0);
  const [isOpen, setIsOpen] = useState(false);

  return (
    <Section title='PORTFOLIO' subtitle='Recent Works'>
      <Grid container spacing={4}>
        {portfolioData.map(({ caption, image }, i) => (
          <InView key={i} threshold={0.5}>
            {({ inView, ref }) => (
              <Grid
                item
                sm={6}
                ref={ref}
                sx={{
                  transitionProperty: 'opacity, transform',
                  transitionDuration: '1500ms',
                  transitionTimingFunction: 'ease-out',
                  transform: `translateY(${inView ? 0 : -100}px)`,
                  opacity: inView ? 1 : 0
                }}
              >
                <Box
                  style={{
                    position: 'relative',
                    cursor: 'pointer'
                  }}
                  onClick={() => {
                    setPhotoIndex(i);
                    setIsOpen(true);
                  }}
                >
                  <Typography
                    variant='caption'
                    color={light}
                    sx={{
                      position: 'absolute',
                      textAlign: 'center',
                      fontWeight: 'bold',
                      left: '5px',
                      bottom: '5px'
                    }}
                  >
                    {caption}
                  </Typography>
                  <img src={image} alt={caption} width='100%' />
                </Box>
              </Grid>
            )}
          </InView>
        ))}
      </Grid>
      {isOpen && (
        <Lightbox
          mainSrc={portfolioData[photoIndex].image}
          imageCaption={portfolioData[photoIndex].caption}
          nextSrc={portfolioData[(photoIndex + 1) % portfolioData.length].image}
          prevSrc={
            portfolioData[
              (photoIndex + portfolioData.length - 1) % portfolioData.length
            ].image
          }
          onCloseRequest={() => setIsOpen(false)}
          onMoveNextRequest={() =>
            setPhotoIndex((photoIndex + 1) % portfolioData.length)
          }
          onMovePrevRequest={() => {
            setPhotoIndex(
              (photoIndex + portfolioData.length - 1) % portfolioData.length
            );
          }}
        />
      )}
    </Section>
  );
};

export default Portfolio;
